*{
  outline: none !important;
  box-sizing: border-box !important;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html, body{
  width: 100%;
  height: 100%;
}


body {
  font-family: "Muller",sans-serif;
}

.container {
  transition: max-width 1s;
}

header ul > li > a[aria-current=page] {
  color: $main-blue;
}
.first-section-js {
  position: relative;
  @media (min-width: 1024px) {
    position: fixed;
    top: 0;
    left: 0;
  }
  width: 100%;
  z-index: 5;
}
.second-section-js {
  margin-top: 100px;
  z-index: 4;
  position: relative;
  @media (min-width: 1024px) {
    opacity: 0;
    margin-top: calc(70vh + 150px);
  }
}